<template>
  <div>
    <div class = "crumbs">
      <el-breadcrumb separator = "/">
        <el-breadcrumb-item><i class = "el-icon-lx-calendar"></i>数据上传</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class = "container">
      <div class = "form-box">
        <el-form ref = "formAdd" :model = "formAdd" label-width = "200px">
          <el-form-item label = "商品主图">
            <el-upload
              class = "upload-demo"
              :action = "$url+'/api/admin/file/pic'"
              name = "image"
              :on-success = "handlesuccess2"
              :on-remove = "handleRemove2"
              :limit = "1"
              :file-list = "fileList2"
              list-type = "picture">
              <el-button size = "small" type = "primary">点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item label = "条形码">
            <el-input v-model = "formAdd.bar_code"></el-input>
          </el-form-item>
          <el-form-item label = "网店名称">
            <el-input v-model = "formAdd.shop_name"></el-input>
          </el-form-item>
          <el-form-item label = "厂家货号">
            <el-input v-model = "formAdd.goods_number"></el-input>
          </el-form-item>
          <el-form-item label = "供应商">
            <el-input v-model = "formAdd.supplier"></el-input>
          </el-form-item>
          <el-form-item label = "客诉问题点">
            <el-input v-model = "formAdd.question"></el-input>
          </el-form-item>
          <el-form-item label = "问题点分析">
            <el-input v-model = "formAdd.question_analysis"></el-input>
          </el-form-item>
          <el-form-item label = "问题点处理方案">
            <el-input v-model = "formAdd.question_solutions"></el-input>
          </el-form-item>
          <el-form-item label = "最后问题批次到货日期">
            <el-date-picker
              v-model = "arrival_date"
              type = "datetime"
              placeholder = "选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label = "客诉首次反馈日期">
            <el-date-picker
              v-model = "feedback_date"
              type = "datetime"
              placeholder = "选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label = "客户ID">
            <el-input v-model = "formAdd.customer_id"></el-input>
          </el-form-item>
          <el-form-item label = "问题图片">
            <el-upload
              class = "upload-demo"
              :action = "$url+'/api/admin/file/pic'"
              name = "image"
              :on-success = "handlesuccess"
              :on-remove = "handleRemove"
              :limit = "4"
              :file-list = "fileList"
              list-type = "picture">
              <el-button size = "small" type = "primary">点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item label = "备注1">
            <el-input v-model = "formAdd.remark1"></el-input>
          </el-form-item>
          <el-form-item label = "备注2">
            <el-input v-model = "formAdd.remark2"></el-input>
          </el-form-item>
          <el-form-item label = "备注3">
            <el-input v-model = "formAdd.remark3"></el-input>
          </el-form-item>
          <el-form-item label = "备注4">
            <el-input v-model = "formAdd.remark4"></el-input>
          </el-form-item>
          <el-form-item label = "视频上传">
            <el-upload
              class = "upload-demo"
              :action = "$url+'/api/admin/file/video'"
              name = "video"
              :on-success = "handlesuccess1"
              :on-remove = "handleRemove1"
              :limit = "1"
              :file-list = "fileList1"
              list-type = "picture">
              <el-button size = "small" type = "primary">点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item>
            <el-button type = "primary" @click = "onSubmit">表单提交</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'baseform',
    data: function () {
      return {
        arrival_date: '', //最后问题批次到货日期
        feedback_date: '', // 客诉首次反馈日期
        fileList: [],//图片接收
        fileList1: [],//视频接收
        fileList2: [],//商品主图修改
        formAdd: {}, // 数据详情添加
        form: {}
      }
    },
    watch: {},
    methods: {
      //上传成功的钩子
      handlesuccess(response, file, fileList,) {
        this.fileList = fileList
        console.log(fileList);
      },
      //移除事件
      handleRemove(file, fileList,) {
        this.fileList = fileList
        console.log(fileList);
      },
      //上传成功的钩子
      handlesuccess1(response, file, fileList1) {
        this.fileList1 = fileList1
        console.log(fileList1);
      },
      //移除事件
      handleRemove1(file, fileList1) {
        this.fileList1 = fileList1
        console.log(fileList1);
      },
      //上传成功的钩子
      handlesuccess2(response, file, fileList2) {
        this.fileList2 = fileList2
        console.log(fileList1);
      },
      //移除事件
      handleRemove2(file, fileList1) {
        this.fileList2 = fileList2
        console.log(fileList1);
      },
      //表单提交
      onSubmit() {
        console.log(this.params);
        console.log(this.fileList);
        console.log(this.fileList1);
        console.log(this.arrival_date);
        console.log(this.feedback_date);
        let params = {
          goods_pic: this.fileList2, // 头部图片
          bar_code: this.formAdd.bar_code,
          shop_name: this.formAdd.shop_name,
          goods_number: this.formAdd.goods_number,
          supplier: this.formAdd.supplier,
          question: this.formAdd.question,
          question_analysis: this.formAdd.question_analysis,
          question_solutions: this.formAdd.question_solutions,
          arrival_date: this.$moment(this.formAdd.arrival_date).format('YYYY-MM-DD hh:mm:ss'),
          feedback_date: this.$moment(this.formAdd.feedback_date).format('YYYY-MM-DD hh:mm:ss'),
          customer_id: this.formAdd.customer_id,
          question_pic: this.fileList, // 图片链接数组
          remark1: this.formAdd.remark1,
          remark2: this.formAdd.remark2,
          remark3: this.formAdd.remark3,
          remark4: this.formAdd.remark4,
          video: this.fileList1, //视频链接
        }
        this.$axios.post('/api/admin/data/dataadd', params, {}).then(res => {
          this.$message({
            message: '上传成功',
            type: 'success'
          });
          this.formAdd = {}
          this.formAdd.arrival_date = ''
          this.formAdd.feedback_date = ''
          this.fileList = []
          this.fileList1 = []
          this.fileList2 = []
          console.log(res);
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>



